
@import "init";

html {
  height: 100%;
}
.page-main {
  .pane .region {
    // This hack helps with scroll offsets for the fixed header,
    // and when users use anchor links from other pages.
    margin-top: -90px;
    padding-top: 90px;
    margin-right: 20px;
    margin-left: 20px;
    overflow: visible;
  }
}

// Blog
article.node--type-blog {
  &.node--view-mode-frontpage {
    margin: 20px auto;
    max-width: 300px;
    width: 100%;
  }
}

// Contact
#pane-contact {
  //ul.leadin {
    //li {
      //width: 100%;
      //max-width: 175px;
      //margin: 20px auto;
    //}
  //}
  #contact-message-feedback-form {
    float: none;
    margin: 20px auto;
    width: auto;
    .form-submit {
      width: 100%;
    }
  }
  ul.leadin {
    float: none;
    padding: 0;
    display: inline-block;
    max-width: 200px;
  }
}

header {
  height: 80px;
  position: fixed;
  top: 0;
}

main {
  margin-top: 80px;
}

#title {
  margin-left: 98px;
  margin-top: 33px;
}

.menu-title {
  position: absolute;
  bottom: 0;
  left: 20px;
  margin: 0;
  padding: 10px 0 5px;
  &:hover {
    text-decoration: none;
  }
}

ul.mobile-menu {
  position: absolute;
  top: 84px;
  left: 0;
  right: 0;
  width: 100%;
  overflow: hidden;
  margin: 0;
  padding: 0;
  li {
    display: block;
    margin: 0;
    padding-bottom: 4px;
    a {
      display: block;
      margin: 4px 4px 0 4px;
      padding: 10px 14px;
    }
  }
}

//Work



.node--type-portfolio {
  &.node--view-mode-full {
    padding-top: 20px;
    .field--name-body {
      float: left;
      p {
        margin-top: 5px;
      }
    }
    .field--name-field-site-screenshots {
      width: 100%;
      height: auto;
      margin-bottom: 25px;
      float: left;
      .cycle-slide {
        width: 100%;
        height: auto;
        img {
          width: 100%;
          height: auto;
        }
      }
    }
  }
  h2 {
    margin-top: 0;
  }
  .portfolio-button {
    margin-top: 6px;
    &:hover {
      text-decoration:none;
    }
  }
  .field.field--name-field-cover-image img {
    height: auto;
    width: 100%;
  }

  .field--name-field-site-screenshots {
    width: 590px;
    max-width: 100%;
    margin: 0 auto;
    .cycle-pager {
      width: 100%;
      img {
        width: 48% !important;
        height: auto;
        margin: 3px 1%;
        float: left;
      }
    }
  }
}


.portfolio-button {
  padding: 5px;
  display: inline-block;
}


#block-views-block-portfolio-block-1 {
  .view-content,
  ul.pager {
    max-width: 300px;
    margin: 0px auto;
  }
}

// Handle short browser windows.
@media screen and (max-height:300px) {
  .page-header {
    position: relative;
    height: 55px;
  }
  h1#title {
    margin-top: 0;
    padding-top: 10px;
  }
  div.mobile-logo {
    height: 50px;
    width: 50px;
    background-size: 90%;
    background-repeat: no-repeat;
  }
  ul.mobile-menu {
    top: 60px;
  }
}

#block-portfoliologos {
  margin-bottom: 15px;
  height: 733px;
  img {
    height: auto !important;
  }
}

p.join-us-button {
  text-align: center;
}

.exception-title {
  display: none;
}

.exception {
  padding-top: 100px;
  box-sizing: border-box;
  .layout-content {
    display: inline-block;
    padding: 20px;
  }
}
